import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import ButtonSignup from "../components/Button/ButtonSignup";
import fruits from "../assets/images/fruits-vegetables-basket-by-oblik-studio.svg";
import Header from "../components/Header/Header";
import "./Login.css";
import { Link } from "react-router-dom";

export const validEmail = new RegExp(
	"^[a-zA-Z0-9._:$!%-]+@[a-zA-Z0-9.-]+.[a-zA-Z]$"
);


class Signup extends Component {


	state={
		userName: "",
		email: "",
		password: "",
		confirmPassword: "",
		errors: [],
		redirect: false,
		userPostSuccess: false,
		submit: false
	};

	validate() {
		var errors= [];
		if (this.state.userName.length === 0) {
			errors.push("Saissez votre nom");
		}
		if (this.state.email.length === 0) {
			errors.push("Saissez votre adresse e-mail");
		}
		if (validEmail.test(this.state.email) === false) {
			errors.push("Saissez une adresse e-mail valide");
		}
		if (this.state.password.length < 6) {
			errors.push("Entre un mot de passe. 6 caràcteres minimum requis");
		}
		if (this.state.password !== this.state.confirmPassword) {
			errors.push("Les mots de passe ne correspondent pas");
		}
		this.setState({errors:errors});
		return errors;
	};

	signUser = () => {
		var errors = [];
		fetch(
			`https://api.lowympact.fr/api/v1/users/${this.state.userName}/${this.state.email}/${this.state.password}`,
			{
				method: "post",
				credentials: "include",
				headers: new Headers({
					Authorization:
					"Bearer 99d8fb95-abdd-4885-bf6c-3a81d8874043",
					"Content-Type": "application/json",
				}),
			}
		)
			.then((response) => response.json())
			.then((res) => {
				console.log(res);
				this.setState({
					userPostSuccess: res.data.success
				});
			});
			if(this.state.userPostSuccess === false || this.state.userPostSuccess === undefined) {
				errors.push("Il y a déjà un compte avec ce mail ou une erreur réseau.");
			}
		this.setState({errors:errors});	
		return errors
	};

	handleSubmit = (e) => {
		e.preventDefault();
		let errors = this.validate();
		console.log("errors: " + errors);
		if(errors.length === 0) {
			errors = this.signUser ();
		}
		if (errors.length === 0) {
			this.setState({ redirect: true });
		}
	};

	displayErrors = () => {
		let errors = <React.Fragment></React.Fragment>
		errors = this.state.errors.map(
			(error) => {
				return(<p>{error}</p>);
			});
		return errors;
	};

	render() {
		
		if (this.state.redirect) {
			return <Redirect to="/history" />;
		}
		console.log("render");
		return (
			<React.Fragment>
				<Link to="/login">
					<Header />
				</Link>
				<div className="logo-fruits">
					<img src={fruits} className="logo" alt="Fruits" />
				</div>
				<Link className="back-button" to="/login">
					{"< retour"}
				</Link>

				<form>
				<label>
						nom
						<input
							value={this.state.userName}
							onChange={(evt) =>
								this.setState({ userName: evt.target.value })
							}
							type="text"
						/>
					</label>
					<label>
						email
						<input
							value={this.state.email}
							onChange={(evt) =>
								this.setState({ email: evt.target.value })
							}
							type="text"
						/>
					</label>
					<label>
						mot de passe
						<input
							value={this.state.password}
							onChange={(evt) =>
								this.setState({ password: evt.target.value })
							}
							type="password"
						/>
					</label>
					<label>
						confirmer mot de passe
						<input
							value={this.state.passwordConfirm}
							onChange={(evt) =>
								this.setState({
									confirmPassword: evt.target.value,
								})
							}
							type="password"
						/>
					</label>
				</form>
				<label className="errors-signup">{this.displayErrors()}</label>
				<div className="button-signup" onClick={this.handleSubmit}>
					<ButtonSignup />
				</div>
			</React.Fragment>
		);
	}
}
export default Signup;
